.area-aluno{
    padding: 42px 0 54px 0;
}

@media(min-width:992px) {
    .area-aluno{
        padding: 80px 0 80px 0;
    }
}

.area-aluno-img {
    width: 100%;
    padding-bottom: 32px;
    border-radius: 4px;
    
}

@media(min-width:992px) {
    .area-aluno-img {
        padding-bottom: 0;
    }   
}

.area-aluno-img-espaco-adicional {
    padding-top: 10px;
}

@media(min-width:992px) {
    .area-aluno-img-espaco-adicional {
        padding-top: 0px;
    }
}

.area-aluno-texto {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
}
@media(min-width:992px) {
    .area-aluno-texto {
        font-size: 24px;
    }
}

.area-aluno-texto__espacamento-adicional {
    padding-bottom: 24px ;
}

@media(min-width:992px) {
    .area-aluno-texto__espacamento-adicional {
        padding-bottom: 35px ;
    }
}

.area-aluno-texto-espacado {
    padding-top: 25px;
}

@media(min-width:992px) {
    .area-aluno-texto-espacado {
        padding-top: 20px;
    }
}

.area-aluno-espaco-rows {
    padding-top: 32px;
}
